        <div class="packages__text">
          <h3 class="subtitle">Locations</h3>
          <h1 class="title">Suggested Location</h1>
          <p class="desc">
            Travel is my life. Since 1999, I've been traveling around the world
            nonstop. If you also love travel, you're in the right place!
          </p>
        </div>
        <div class="packages__btns">
          <div @click="indexSub" class="packages__btns-prev packages__btn">
            <div class="arrow-prev-top arrow"></div>
            <div class="arrow-prev-bottom arrow"></div>
          </div>
          <div @click="indexAdd" class="packages__btns-next packages__btn">
            <div class="arrow-next-top arrow"></div>
            <div class="arrow-next-bottom arrow"></div>
          </div>
        </div>
      </div>
      <!-- First I wanted carousel (slider), but then thought about just a v-show v-if option, that's easier and more responsive-->
      <div class="packages__carousel">
        <transition name="carousel">
          <div v-if="index == 0" class="carousel">
            <div v-for="item in items1" :key="item.index" class="carousel-item">
              <img
                :src="require('../assets/img/Packages/' + item.bg + '.png')"
                alt=""
                class="carousel-item-bg"
              />
              <div class="carousel-item-info">
                <div class="carousel-item-desc">
                  <p class="place">{{ item.place }}</p>
                  <p class="location">{{ item.location }}</p>
                </div>
                <div class="carousel-item-rating">
                  <img
                    :src="
                      require('../assets/img/Packages/star' +
                        item.stars +
                        '.svg')
                    "
                    alt=""
                    class="rating"
                  />
                  <p class="price">${{ item.price }}</p>
                </div>
              </div>
            </div>
          </div>
        </transition>
        <transition name="carousel">
          <div v-if="index == 1" class="carousel">
            <div v-for="item in items2" :key="item.index" class="carousel-item">
              <img
                :src="require('../assets/img/Packages/' + item.bg + '.png')"
                alt=""
                class="carousel-item-bg"
              />
              <div class="carousel-item-info">
                <div class="carousel-item-desc">
                  <p class="place">{{ item.place }}</p>
                  <p class="location">{{ item.location }}</p>
                </div>
                <div class="carousel-item-rating">
                  <img
                    :src="
                      require('../assets/img/Packages/star' +
                        item.stars +
                        '.svg')
                    "
                    alt=""
                    class="rating"
                  />
                  <p class="price">${{ item.price }}</p>
                </div>
              </div>
            </div>
          </div>
        </transition>
        <transition name="carousel">
          <div v-if="index == 2" class="carousel">
            <div v-for="item in items3" :key="item.index" class="carousel-item">
              <img
                :src="require('../assets/img/Packages/' + item.bg + '.png')"
                alt=""
                class="carousel-item-bg"
              />
              <div class="carousel-item-info">
                <div class="carousel-item-desc">
                  <p class="place">{{ item.place }}</p>
                  <p class="location">{{ item.location }}</p>
                </div>
                <div class="carousel-item-rating">
                  <img
                    :src="
                      require('../assets/img/Packages/star' +
                        item.stars +
                        '.svg')
                    "
                    alt=""
                    class="rating"
                  />
                  <p class="price">${{ item.price }}</p>
                </div>
              </div>
            </div>
          </div>
        </transition>
      </div>
    </div>
    <main class="packages-main">
      <div class="packages-video">
        <video
          class="video"
          src="../assets/img/Packages/Notre-dame.mp4"
          muted
          loop
          autoplay
        ></video>
        <img
          class="packages-video-bg"
          src="../assets/img/Packages/Vector.png"
          alt=""
        />
        <div class="packages-mobile-bg"></div>
        <div class="packages-main-info">
          <h3 class="subtitle">Take a Tour</h3>
          <h1 class="title">Discover Our Travel Guideline</h1>
