</template>

<script>
export default {
  data() {
    return {
      items1: [
        {
          index: 1,
          place: "Bali Handara Gate",
          location: "Indonesia",
          price: 1200,
          stars: "5",
          bg: "Indonesia",
        },
        {
          index: 2,
          place: "Buddhist shrine",
          location: "Tokyo, Japan",
          price: 1199,
          stars: "5",
          bg: "Japan",
        },
        {
          index: 3,
          place: "Rialto Bridge",
          location: "Italy",
          price: 1459,
          stars: "4",
          bg: "Italy",
        },
      ],
      items2: [
        {
          index: 1,
          place: "Egyptian pyramids",
          location: "Saqqara, Egypt",
          price: 600,
          stars: "2",
          bg: "Egypt",
        },
        {
          index: 2,
          place: "Taj Mahal",
          location: "Agra, India",
          price: 800,
          stars: "3",
          bg: "India",
        },
        {
          index: 3,
          place: "Alps, Spain",
          location: "Spain",
          price: 2200,
          stars: "5",
          bg: "Spain",
        },
      ],
      items3: [
        {
          index: 1,
          place: "Eiffel Tower",
          location: "Paris, France",
          price: 3600,
          stars: "4",
          bg: "France",
        },
        {
          index: 2,
          place: "Big Ben",
          location: "London, UK",
          price: 2800,
          stars: "4",
          bg: "UK",
        },
        {
          index: 3,
          place: "Liberty Statue",
          location: "New-York, USA",
          price: 4200,
          stars: "1",
          bg: "Newyork",
        },
      ],
      index: 0,
    };
  },
  methods: {
    indexSub() {
      if (this.index > 0) {
        this.index--;
      } else {
        this.index = 2;
      }
    },
    indexAdd() {
      if (this.index < 2) {
        this.index++;
      } else {
        this.index = 0;
      }
    },
  },
};
</script>

<style>
.packages {
  padding-top: 75px;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 30px;
}

.packages__info {
